<script>
import Datepicker from "vuejs-datepicker/dist/vuejs-datepicker.esm.js";
	export default {
    name: "Home",

    components: {
      Datepicker,
    },
    data() {
      return {
        format: 'yyyy-MM-dd',
        current_post: {},
        post_view:{},
        message: '',
        searchQuery:'',
        searchOption:'',
        etudiants: [],
        filieres: [],
        nom: '',
        prenom: '',
        filiere: '',
        lieuNaissance: '',
        dateNaissance: '',
        sexe: '',
        telephone: '',
        cin: '',
        emailPersonnel: '',
        emailInstitutionnel: ''
      }
    },
    methods: {
      toggleView(post) {
        this.post_view = JSON.parse(JSON.stringify(post));
        $('#viewModal').modal('show')
      },

      toggleUpdate(post) {
        this.current_post = JSON.parse(JSON.stringify(post));
        $('#updateModal').modal('show')
      },

      toggleInsert() {
        $('#insertModal').modal('show')
      },

      supprimer(post) {
        this.$SHARED.dispatchAsyncDelete(this.$SHARED.services.SUPPRIMER_ETUDIANT,
            {id: post._id}).then(data => {
          this.message = data.message;
        }, () => {})
      },

			Ajouter() {
				this.$SHARED.dispatchAsyncPost(this.$SHARED.services.AJOUTER_ETUDIANT, {
				  nom: this.nom,
				  prenom: this.prenom,
          lieuNaissance: this.lieuNaissance,
          dateNaissance: this.dateNaissance,
          sexe: this.sexe,
          filiere: this.filiere,
          telephone: this.telephone,
          cin: this.cin,
          emailPersonnel: this.emailPersonnel,
          emailInstitutionnel: this.emailInstitutionnel
				}).then( data => {
					this.message = data.message;
				}, () => {})
			},

      modifier(current_post){
        this.$SHARED.dispatchAsyncPut(this.$SHARED.services.MODIFIER_ETUDIANT,
            {
              _id: current_post._id,
              nom: current_post.nom,
              prenom: current_post.prenom,
              lieuNaissance: current_post.lieuNaissance,
              dateNaissance: current_post.dateNaissance,
              sexe: current_post.sexe,
              filiere: current_post.filiere,
              telephone: current_post.telephone,
              cin: current_post.cin,
              emailPersonnel: current_post.emailPersonnel,
              emailInstitutionnel: current_post.emailInstitutionnel
            }).then( data => {
          this.message = data.message;
        }, () => {})
      },

      getALL(){
        this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_ETUDIANTS ).then( data => {
          this.etudiants = data;
        }, () => {})
      },

      getFilieres(){
        this.$SHARED.dispatchAsyncGet(this.$SHARED.services.LISTER_FILIERES ).then( data => {
          this.filieres = data;
        }, () => {})
      },

    },
    computed: {
      resultQuery()
      {
        if (this.searchQuery) {
          return this.etudiants.filter((item) => {
            return this.searchQuery.toLowerCase().split(' ').every(v => item.nom.toLowerCase().includes(v))
          })
        } else {
          return this.etudiants;
        }
      }
    },
    mounted() {
      this.getALL()
      this.getFilieres()
    }


  }
</script>

